@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@200;400;700&display=swap);
body {
  margin: 0;
  font-family: 'Barlow', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {
  text-align: center;
}

.App-logo {
  width: 180px;
  pointer-events: none;
  margin: 0 auto;
}

/* @media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
} */

.header {
  display: grid;
  grid-template-columns: 250px 1fr;
  -webkit-align-items: center;
          align-items: center;
}

.App-header {
  background-color: #022d5b;
  color: white;
  text-align:left;
  padding: 10px 20px;
}

.App-header img {
  background: white;
}

.App-header h1 {
  margin-bottom: 5px;
  margin-top: 15px;
  font-size: 40px;
  font-weight: 800;
  letter-spacing: 1px;
}

.App-header p {
  margin-top: 10px;
  text-transform: uppercase;
  letter-spacing: 5px;
  font-size: 20px;
  font-weight: 200;
}

.App-link {
  color: #61dafb;
}

.recharts-surface {
  overflow: visible;
}

p.explore-header {
  font-size: 28px;
  font-weight: bold;
}

.chart-container {
  padding-top: 35px;
}

.dropdown-container {
  padding: 3%;
  background: #f1f1f1;
}

select.dropdown {
  font-family: inherit;
  font-size: 17px;
}
